@import "../NewTaskForm/NewTaskForm.scss";


.footer {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 10px 15px;
	padding-left: 0;
	height:35px;
	text-align: center;
	font-size: 15px;
	border-top: 1px solid #e6e6e6;
	color: gray;
    margin-bottom: 5px;
    // padding-bottom: 15px;
}

.footer:before {
	content: '';
	position: absolute;
	z-index: -1;
	right: 0;
	bottom: 0;
	left: 0;
	height: 50px;
	overflow: hidden;
	box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2),
	            0 8px 0 -3px #f6f6f6,
	            0 9px 1px -3px rgba(0, 0, 0, 0.2),
	            0 16px 0 -6px #f6f6f6,
	            0 17px 2px -6px rgba(0, 0, 0, 0.2);
}

@media screen and (width<480px) {
	.footer {
		display: flex;
		height: 150px;
		flex-wrap: wrap;
	}
}
